import React from "react";
import {
  Box,
  Button,
  Flex,
  Image,
  Input,
  InputGroup,
  InputLeftAddon,
  Text,
  Textarea,
  useToast,
} from "@chakra-ui/react";
import banner from "./banner.png";
import men from "./men.jpg";
import women from "./women.jpg";
import electronic from "./electronic.jpg";
import { peopleChoicesProducts } from "../../apis/PeopleChoicesProducts";
import { jeweleryProducts } from "../../apis/JeweleryProducts";
import { useQuery } from "react-query";
import HomePageCard from "../../components/HomePageCard";
import Loading from "../../components/loading/Loading";
import contact from "./contact.png";
import { FormControl, FormLabel } from "@chakra-ui/react";
import { useNavigate } from "react-router-dom";
import ScrollToTopButton from "../../components/ScrollToTopButton";
import * as yup from "yup";
import { useFormik, FormikProvider, Form } from "formik";
import emailjs from "emailjs-com";

const Home = () => {
  const navigate = useNavigate();
  const toast = useToast();

  const { data } = useQuery("peopleChoiceProducts", peopleChoicesProducts, {
    staleTime: 60000000000,
  });

  const { data: jewelery } = useQuery("jeweleryProducts", jeweleryProducts, {
    staleTime: 60000000000,
  });

  const contactFormSchema = yup.object({
    first_name: yup.string().required("First name is required"),
    last_name: yup.string().required("Last name is required"),
    email: yup
      .string()
      .email("Please fill a valid Email format")
      .required("Please fill your Email"),
    phone_number: yup
      .number()
      .typeError("That doesn't look like a phone number")
      .positive("A phone number can't start with a minus")
      .integer("A phone number can't include a decimal point")
      .min(8, "Phone number must be at least 8 characters")
      .required("A phone number is required"),
    suggestion: yup.string().required("Suggestion is required"),
  });

  const contactForm = useFormik({
    initialValues: {
      first_name: "",
      last_name: "",
      email: "",
      phone_number: "",
      suggestion: "",
    },

    validationSchema: contactFormSchema,
    onSubmit: async (values, { setErrors, setSubmitting, resetForm }) => {
      await emailjs.send(
        "service_xgyex8k",
        "template_xtne10s",
        values,
        "user_rIEYPjgfHTybq43Rv2QC8"
      );
      resetForm();
      setSubmitting(false);
    },
  });

  const { errors, isSubmitting, handleSubmit, getFieldProps } = contactForm;
  return (
    <Flex mt="110px" mb="40px" direction="column">
      {data && jewelery ? (
        <Flex mx={{ base: "30px", lg: "100px" }} direction="column">
          <ScrollToTopButton />
          <Flex
            align="center"
            justify="center"
            direction={{ base: "column", xl: "row" }}
            gridGap="40px"
          >
            <Box w="850px">
              <Flex
                direction="column"
                gridGap="10px"
                textAlign={{ base: "center", xl: "left" }}
              >
                <Text fontWeight="900" fontSize="25px">
                  Welcome From Robin Store
                </Text>
                <Text mr={{ xl: "200px" }}>
                  Lorem ipsum dolor sit amet consectetur adipisicing elit. Porro
                  totam similique nam corrupti quisquam labore aliquam
                  reiciendis ad saepe quidem fugit iustoatis nesciunt cumque
                  maiores illo omnis nihil eligendi, est saepe eaque iusto
                  perferendis, odit, tempore reiciendis soluta.
                </Text>
                <Flex
                  gridGap="20px"
                  mt="10px"
                  justify={{ base: "center", xl: "flex-start" }}
                >
                  <Button
                    colorScheme="teal"
                    onClick={() => navigate("/products")}
                  >
                    Shop Now
                  </Button>
                  <Button variant="outline" colorScheme="teal">
                    <a href="#contactUs">Contact Us</a>
                  </Button>
                </Flex>
              </Flex>
            </Box>
            <Image src={banner} w="500px" borderRadius="20px" />
          </Flex>

          <Flex mt="100px" direction="column">
            <Text textAlign="center" fontSize="25px" fontWeight="700">
              Top Categories
            </Text>
            <Flex
              mt="50px"
              direction={{ base: "column", xl: "row" }}
              justify="center"
              align="center"
            >
              <Image src={men} w="33.3%" cursor="pointer" />
              <Image src={women} w="33.3%" cursor="pointer" />
              <Image src={electronic} w="33.3%" cursor="pointer" />
            </Flex>
          </Flex>

          <Flex mt="100px" direction="column">
            <Text textAlign="center" fontSize="25px" fontWeight="700">
              People Choices
            </Text>
            <Flex
              mt="50px"
              justify="space-evenly"
              flexWrap="wrap"
              gridGap="25px"
            >
              {data?.length > 0 &&
                data.map((product) => (
                  <HomePageCard
                    key={product.id}
                    id={product.id}
                    image={product.image}
                    title={product.title}
                    price={product.price}
                    count={product.rating.count}
                    product={product}
                  />
                ))}
            </Flex>
          </Flex>

          <Flex mt="100px" direction="column">
            <Text textAlign="center" fontSize="25px" fontWeight="700">
              Jewelerys You Can Get
            </Text>
            <Flex
              mt="50px"
              justify="space-evenly"
              flexWrap="wrap"
              gridGap="25px"
            >
              {jewelery?.length > 0 &&
                jewelery.map((product) => (
                  <HomePageCard
                    key={product.id}
                    id={product.id}
                    image={product.image}
                    title={product.title}
                    price={product.price}
                    count={product.rating.count}
                    product={product}
                  />
                ))}
            </Flex>
          </Flex>

          <Flex mt="100px" direction="column" id="contactUs">
            <Text textAlign="center" fontSize="25px" fontWeight="700">
              Keep in touch with us
            </Text>
            <Flex mt="50px" justify="space-evenly" align="center">
              <Image
                src={contact}
                w="500px"
                display={{ base: "none", xl: "block" }}
              />

              <Flex
                direction="column"
                gridGap="15px"
                boxShadow="2px 1px 30px -11px rgba(0,0,0,0.56)"
                p="40px"
                borderRadius="20px"
              >
                <Text
                  fontWeight="700"
                  fontSize="20px"
                  mb="15px"
                  textAlign="center"
                >
                  Contact Form
                </Text>
                <FormikProvider value={contactForm}>
                  <Form autoComplete="off" onSubmit={handleSubmit}>
                    <Flex direction="column" gridGap="15px">
                      <Flex gridGap="10px">
                        <FormControl>
                          <FormLabel>First Name</FormLabel>
                          <Input {...getFieldProps("first_name")} />
                          <Text color="red" fontSize="12px">
                            {errors.first_name}
                          </Text>
                        </FormControl>
                        <FormControl>
                          <FormLabel>Last Name</FormLabel>
                          <Input {...getFieldProps("last_name")} />
                          <Text color="red" fontSize="12px">
                            {errors.last_name}
                          </Text>
                        </FormControl>
                      </Flex>
                      <FormControl id="email">
                        <FormLabel>Your Email</FormLabel>
                        <Input
                          type="email"
                          placeholder="example@gmail.com"
                          {...getFieldProps("email")}
                        />
                        <Text color="red" fontSize="12px">
                          {errors.email}
                        </Text>
                      </FormControl>
                      <FormControl>
                        <FormLabel>Your Phone Number</FormLabel>
                        <InputGroup>
                          <InputLeftAddon children="+95" />
                          <Input
                            type="number"
                            placeholder="9*********"
                            {...getFieldProps("phone_number")}
                          />
                        </InputGroup>
                        <Text color="red" fontSize="12px">
                          {errors.phone_number}
                        </Text>
                      </FormControl>
                      <FormControl>
                        <FormLabel>What do you want to suggest?</FormLabel>
                        <Textarea {...getFieldProps("suggestion")} />
                        <Text color="red" fontSize="12px">
                          {errors.suggestion}
                        </Text>
                      </FormControl>
                      <Button
                        colorScheme="teal"
                        type="submit"
                        mt="15px"
                        isLoading={isSubmitting}
                        onClick={() =>
                          toast({
                            title: "Well received your Email",
                            status: "success",
                            duration: 4000,
                            isClosable: true,
                          })
                        }
                      >
                        Submit
                      </Button>
                    </Flex>
                  </Form>
                </FormikProvider>
              </Flex>
            </Flex>
          </Flex>
        </Flex>
      ) : (
        <Loading />
      )}
    </Flex>
  );
};

export default Home;
