import React from "react";
import {
  Modal,
  ModalOverlay,
  ModalContent,
  ModalFooter,
  ModalBody,
  Button,
  Flex,
  Text,
} from "@chakra-ui/react";
import { AiOutlineCheckCircle } from "react-icons/ai";
import { useNavigate } from "react-router-dom";
import { useDispatch } from "react-redux";
import { emptyCart } from "../redux/actions/CartActions";

const CheckoutModal = ({ onClose, isOpen }) => {
  const navigate = useNavigate();
  const dispatch = useDispatch();

  const goShoppingBtn = () => {
    onClose();
    navigate("/");
    dispatch(emptyCart());
  };

  return (
    <div>
      <Modal
        isOpen={isOpen}
        onClose={onClose}
        isCentered
        closeOnOverlayClick={false}
      >
        <ModalOverlay />
        <ModalContent>
          <ModalBody>
            <Flex align="center" px="15px" py="30px" direction="column">
              <AiOutlineCheckCircle
                style={{ fontSize: "70px", color: "#f24d92" }}
              />
              <Text fontSize="20px" fontWeight="900" mt="10px" color="#f24d92">
                Order Successful
              </Text>
              <Text opacity="0.7" mt="5px">
                Thanks for shopping with us
              </Text>
              <Button
                colorScheme="teal"
                mt="20px"
                size="sm"
                onClick={goShoppingBtn}
              >
                Go Shopping
              </Button>
            </Flex>
          </ModalBody>
          <ModalFooter></ModalFooter>
        </ModalContent>
      </Modal>
      ;
    </div>
  );
};

export default CheckoutModal;
