import {
  Button,
  Divider,
  Flex,
  Image,
  Tag,
  Text,
  useToast,
} from "@chakra-ui/react";
import React, { useEffect } from "react";
import { useDispatch, useSelector } from "react-redux";
import { useParams, useNavigate } from "react-router-dom";
import {
  getRelatedProducts,
  getSingleProduct,
} from "../../redux/actions/ProductActions";
import { AiFillHeart } from "react-icons/ai";
import { addToCart } from "../../redux/actions/CartActions";
import ProductCard from "../../components/ProductCard";
import Loading from "../../components/loading/Loading";

const Detail = () => {
  const { loading, product } = useSelector((state) => state.product);

  const {
    loading: relatedLoading,
    products: relatedProducts,
  } = useSelector((state) => state.relatedProducts);

  const dispatch = useDispatch();
  const { id } = useParams();
  const navigate = useNavigate();
  const toast = useToast();

  const addToCartBtn = () => {
    dispatch(addToCart(product.id, product, product.price, 1));
    toast({
      title: "Added to cart.",
      status: "success",
      duration: 3000,
      isClosable: true,
    });
  };

  const buyNowBtn = () => {
    dispatch(addToCart(product.id, product, product.price, 1));
    navigate("/cart");
  };

  useEffect(() => {
    dispatch(getSingleProduct(id));
    // eslint-disable-next-line
  }, []);

  useEffect(() => {
    if (product.category) {
      dispatch(getRelatedProducts(product.category));
    }
    // eslint-disable-next-line
  }, [product.category]);

  return (
    <Flex mt="110px" mx="120px" mb="40px">
      {loading || relatedLoading ? (
        <Loading />
      ) : (
        <Flex direction="column" w="100%" gridGap="30px">
          {/* Product Detail */}
          <Flex justify="space-evenly" w="100%">
            <Image src={product.image} w="300px"></Image>
            <Flex direction="column" w="400px" mt="50px" gridGap="15px">
              <Flex align="center" gridGap="15px">
                <Tag size="md" variant="outline">
                  Category - {product.category}
                </Tag>
                <AiFillHeart
                  style={{
                    fontSize: "22px",
                    color: "red",
                    cursor: "pointer",
                  }}
                />
              </Flex>
              <Text fontWeight="700" fontSize="20px" display="inline-block">
                {product.title}
              </Text>
              <Text fontWeight="500" fontSize="16px">
                $ {product.price}
              </Text>
              <Text fontSize="16px">{product.description}</Text>
              <Flex gridGap="20px">
                <Button
                  variant="solid"
                  colorScheme="teal"
                  onClick={addToCartBtn}
                >
                  Add to Cart
                </Button>
                <Button
                  variant="outline"
                  colorScheme="teal"
                  onClick={buyNowBtn}
                >
                  Buy Now
                </Button>
              </Flex>
            </Flex>
          </Flex>

          <Divider />
          {/* Related Products*/}
          <Flex direction="column" gap="30px" align="center">
            <Text fontSize="25px" fontWeight="700">
              Related Products
            </Text>
            <Flex
              mb="40px"
              wrap="wrap"
              align="center"
              justify="center"
              gridGap="20px"
            >
              {relatedProducts.map((product) => (
                <ProductCard
                  key={product.id}
                  id={product.id}
                  image={product.image}
                  title={product.title}
                  price={product.price}
                  count={product.rating.count}
                  product={product}
                />
              ))}
            </Flex>
          </Flex>
        </Flex>
      )}
    </Flex>
  );
};

export default Detail;
