import {
  Button,
  Divider,
  Flex,
  Image,
  Text,
  useDisclosure,
} from "@chakra-ui/react";
import React from "react";
import { AiFillDelete } from "react-icons/ai";
import { useDispatch, useSelector } from "react-redux";
import {
  addToCart,
  emptyCart,
  removeFromCart,
} from "../../redux/actions/CartActions";
import {
  Table,
  Thead,
  Tbody,
  Tr,
  Th,
  Td,
  TableCaption,
} from "@chakra-ui/react";
import { Link } from "react-router-dom";
import CheckoutModal from "../../components/CheckoutModal";

const Cart = () => {
  const dispatch = useDispatch();
  const { cartItems } = useSelector((state) => state.cart);
  const { isOpen, onOpen, onClose } = useDisclosure();

  const removeFromCartBtn = (id) => {
    dispatch(removeFromCart(id));
  };

  const decreaseQty = (item) => {
    if (item.qty > 1) {
      dispatch(
        addToCart(item.productId, item.product, item.price, item.qty - 1)
      );
    }
  };

  const increaseQty = (item) => {
    if (item.qty <= 50) {
      dispatch(
        addToCart(item.productId, item.product, item.price, item.qty + 1)
      );
    }
  };

  const itemPrice = cartItems.reduce((accumulator, current) => {
    return accumulator + current.price * current.qty;
  }, 0);

  const discountPrice = itemPrice > 500 ? itemPrice * 0.1 : 0; //500$ -> 10% discount

  const taxPrice = itemPrice * 0.03; //3% tax

  const deliveryPrice = itemPrice > 0 ? 10 : 0;

  const totalPrice = itemPrice + taxPrice + deliveryPrice - discountPrice;

  const emptyCartBtn = () => {
    dispatch(emptyCart());
  };

  return (
    <Flex mt="110px" mb="40px" mx="100px" direction="row" gridGap="70px">
      <Table variant="simple">
        <TableCaption>
          Thanks for shopping with us. Here is your cart!
          <Text color="blue.500" cursor="pointer" onClick={emptyCartBtn}>
            Clear Your Cart
          </Text>
        </TableCaption>
        <Thead>
          <Tr>
            <Th>Image</Th>
            <Th>Name</Th>
            <Th>Quantity</Th>
            <Th>Total Price</Th>
            <Th>Actions</Th>
          </Tr>
        </Thead>
        {cartItems.length === 0 && (
          <Text
            fontSize="18px"
            fontWeight="600"
            align="center"
            mt="50px"
            ml="10px"
            mr="3px"
          >
            Your Cart is empty!{" "}
            <Link to="/" style={{ color: "teal" }}>
              Go shopping ...
            </Link>
          </Text>
        )}
        <Tbody>
          {cartItems.map((item) => (
            <Tr>
              <Td>
                <Image src={item.product.image} h="70px" />
              </Td>
              <Td>
                <Text>{item.product.title}</Text>
              </Td>
              <Td>
                <Flex align="center" gridGap="10px">
                  <Button onClick={() => decreaseQty(item)}>-</Button>
                  <Button>{item.qty}</Button>
                  <Button onClick={() => increaseQty(item)}>+</Button>
                </Flex>
              </Td>
              <Td>
                <Text>$ {(item.price * item.qty).toFixed(2)}</Text>
              </Td>
              <Td>
                <AiFillDelete
                  style={{ fontSize: "30px", cursor: "pointer" }}
                  onClick={() => removeFromCartBtn(item.productId)}
                />
              </Td>
            </Tr>
          ))}
        </Tbody>
      </Table>

      <Flex direction="column" gridGap="20px">
        <Text fontSize="20px" fontWeight="700">
          Order Summary
        </Text>
        <Flex
          direction="column"
          w="350px"
          shadow="lg"
          p="20px"
          borderRadius="10px"
          gridGap="15px"
        >
          <Flex justify="space-between">
            <Text fontWeight="500" display="inline-block">
              Total Price
            </Text>
            <Text fontWeight="500" display="inline-block">
              $ {itemPrice.toFixed(2)}
            </Text>
          </Flex>

          <Flex justify="space-between">
            <Text fontWeight="500" display="inline-block">
              Discount Price
            </Text>
            <Text fontWeight="500" display="inline-block">
              $ {discountPrice.toFixed(2)}
            </Text>
          </Flex>

          <Flex justify="space-between">
            <Text fontWeight="500" display="inline-block">
              Tax Price
            </Text>
            <Text fontWeight="500" display="inline-block">
              $ {taxPrice.toFixed(2)}
            </Text>
          </Flex>

          <Flex justify="space-between">
            <Text fontWeight="500" display="inline-block">
              Delivery Price
            </Text>
            <Text fontWeight="500" display="inline-block">
              $ {deliveryPrice}
            </Text>
          </Flex>
          <Divider></Divider>
          <Flex justify="space-between">
            <Text fontWeight="500" display="inline-block">
              Total Price
            </Text>
            <Text fontWeight="500" display="inline-block">
              $ {totalPrice.toFixed(2)}
            </Text>
          </Flex>
        </Flex>
        <Button colorScheme="teal" disabled={cartItems <= 0} onClick={onOpen}>
          Checkout Now
        </Button>
        <CheckoutModal isOpen={isOpen} onClose={onClose} />
      </Flex>
    </Flex>
  );
};

export default Cart;
