import { Flex, Text, Box, Button } from "@chakra-ui/react";
import React from "react";
import { AiFillStar } from "react-icons/ai";
import { IoEyeSharp } from "react-icons/io5";
import { FaShoppingCart } from "react-icons/fa";
import { useDispatch } from "react-redux";
import { useNavigate } from "react-router-dom";
import { addToCart } from "../redux/actions/CartActions";
import { useToast } from "@chakra-ui/react";
import { LazyLoadImage } from "react-lazy-load-image-component";

const HomePageCard = ({ id, image, title, price, count, product }) => {
  const dispatch = useDispatch();
  const navigate = useNavigate();
  const toast = useToast();

  const addToCartBtn = () => {
    dispatch(addToCart(id, product, price, 1));
    toast({
      title: "Added to cart.",
      status: "success",
      duration: 2000,
      isClosable: true,
    });
  };

  const detailBtn = (id) => {
    navigate(`/product/${id}`);
  };

  return (
    <Flex
      direction="column"
      w="280px"
      p="25px"
      borderRadius="20px"
      justify="space-between"
      gridGap="10px"
      boxShadow="2px 1px 19px -2px rgba(0,0,0,0.56)"
    >
      <Flex height="150px" w="130px" alignSelf="center">
        <LazyLoadImage src={image} />
      </Flex>
      <Text fontWeight="500">{title}</Text>
      <Flex direction="column" gap="10px">
        <Flex align="end" justify="space-between">
          <Flex direction="column">
            <Text fontWeight="900" color="green">
              $ - {price}
            </Text>
            <Box display="flex" mt="2" alignItems="center">
              {[1, 2, 3, 4, 5].map((_, i) => (
                <AiFillStar key={i} style={{ color: "gold" }} />
              ))}
              <Box as="span" ml="2" color="gray.600" fontSize="sm">
                {count} views
              </Box>
            </Box>
          </Flex>
          <Button colorScheme="teal" size="md" onClick={addToCartBtn}>
            <FaShoppingCart />
          </Button>
        </Flex>
        <Button colorScheme="orange" size="md" onClick={() => detailBtn(id)}>
          <IoEyeSharp />
        </Button>
      </Flex>
    </Flex>
  );
};

export default HomePageCard;
